// ./react-redux-client/src/components/FeedbackForm.js
import React from 'react';
import { FormGroup,ControlLabel,FormControl,Button } from 'react-bootstrap';

const FeedbackForm = (props) => {
  return (
    <form className="form form-horizontal" id="addFeedbackForm" onSubmit={props.addFeedback}>
    <div className="row">
    <h3 className="centerAlign">Add Your Feedback</h3>
    <div className="col-md-12">
    <FormGroup>
          <ControlLabel>Feedback: </ControlLabel>
            <FormControl
              type="text" placeholder="Enter feedback"
              name="feedbackText"
               />
        </FormGroup>
        </div>
        <div className="col-md-12">
        <FormGroup>
              <ControlLabel>Description: </ControlLabel>
                <FormControl
                  componentClass="textarea" placeholder="Enter description"
                  name="feedbackDesc"
                   />
            </FormGroup>
            </div>
          </div>
          <FormGroup>
              <Button type="submit" bsStyle="success" bsSize="large" block>Submit</Button>
          </FormGroup>
    </form>
  );
}

export default FeedbackForm;



