// ./react-redux-client/src/reducers/feedbackReducer.js
const INITIAL_STATE = {
  feedbacks:[],
  feedback:null,
  isFetching: false,
  error: null,
  successMsg:null,
  showDeleteModal: false,
  feedbackToDelete: null,
  showEditModal: false,
  feedbackToEdit: null,
  newFeedback: null
}

export  const feedbackReducer = (currentState = INITIAL_STATE, action) => {
  switch (action.type) {
    case 'FETCH_FEEDBACKS_REQUEST':
          return {
            ...currentState,
            feedbacks:[],
            feedback:null,
            isFetching: true,
            error: null,
            successMsg:null,
            showDeleteModal: false,
            feedbackToDelete: null,
            showEditModal: false,
            feedbackToEdit: null,
          }

    case 'FETCH_FEEDBACKS_SUCCESS':
          return {
            ...currentState,
            feedbacks:action.feedbacks,
            feedback:null,
            isFetching: false,
            error: null,
            successMsg:action.message,
            showDeleteModal: false,
            feedbackToDelete: null,
            showEditModal: false,
            feedbackToEdit: null,
          }

    case 'FETCH_FEEDBACKS_FAILED':
          return {
            ...currentState,
            feedbacks:[],
            feedback:null,
            isFetching: false,
            error: action.error,
            successMsg:null,
            showDeleteModal: false,
            feedbackToDelete: null,
            showEditModal: false,
            feedbackToEdit: null,
          }

    case 'FETCH_FEEDBACK_REQUEST':
          return {
            ...currentState,
            feedbacks:currentState.feedbacks,
            feedback:null,
            isFetching: true,
            error: null,
            successMsg:null,
            showDeleteModal: false,
            feedbackToDelete: null,
            showEditModal: false,
            feedbackToEdit: null,
          }

    case 'FETCH_FEEDBACK_SUCCESS':
          return {
            ...currentState,
            feedbacks:currentState.feedbacks,
            feedback:action.feedback,
            isFetching: false,
            error: null,
            successMsg:action.message,
            showDeleteModal: false,
            feedbackToDelete: null,
            showEditModal: false,
            feedbackToEdit: null,
          }

    case 'FETCH_FEEDBACK_FAILED':
          return {
            ...currentState,
            feedbacks:[],
            feedback:null,
            isFetching: false,
            error: action.error,
            successMsg:null,
            showDeleteModal: false,
            feedbackToDelete: null,
            showEditModal: false,
            feedbackToEdit: null,
          }

    case 'ADD_NEW_FEEDBACK_REQUEST':
          return {
            ...currentState,
            feedbacks:currentState.feedbacks,
            feedback:null,
            isFetching: true,
            error: null,
            successMsg:null,
            showDeleteModal: false,
            feedbackToDelete: null,
            showEditModal: false,
            feedbackToEdit: null,
            newFeedback: action.feedback
          }

    case 'ADD_NEW_FEEDBACK_REQUEST_FAILED':
          return {
            ...currentState,
            feedbacks:currentState.feedbacks,
            feedback:null,
            isFetching: true,
            error: action.error,
            successMsg:null,
            showDeleteModal: false,
            feedbackToDelete: null,
            showEditModal: false,
            feedbackToEdit: null,
            newFeedback: null
          }

    case 'ADD_NEW_FEEDBACK_REQUEST_SUCCESS':
          const nextState =  {
            ...currentState,
            feedbacks:[...currentState.feedbacks, action.feedback],
            feedback:null,
            isFetching: false,
            error: null,
            successMsg:action.message,
            showDeleteModal: false,
            feedbackToDelete: null,
            showEditModal: false,
            feedbackToEdit: null,
            newFeedback: action.feedback
          }
        return nextState;

  case 'SHOW_EDIT_MODAL':
        return {
          ...currentState,
          feedbacks:currentState.feedbacks,
          feedback:null,
          isFetching: false,
          error: null,
          successMsg:null,
          showDeleteModal: false,
          feedbackToDelete: null,
          showEditModal: true,
          feedbackToEdit: action.feedback,
          newFeedback: null
        }

  case 'HIDE_EDIT_MODAL':
        return {
          ...currentState,
          feedbacks:currentState.feedbacks,
          feedback:null,
          isFetching: false,
          error: null,
          successMsg:null,
          showDeleteModal: false,
          feedbackToDelete: null,
          showEditModal: false,
          feedbackToEdit: null,
          newFeedback: null
        }

    case 'EDIT_FEEDBACK_REQUEST':
          return {
            ...currentState,
            feedbacks:currentState.feedbacks,
            feedback:null,
            isFetching: true,
            error: null,
            successMsg:null,
            showDeleteModal: false,
            feedbackToDelete: null,
            showEditModal: true,
            feedbackToEdit: action.feedback,
            newFeedback: null
          }

    case 'EDIT_FEEDBACK_SUCCESS':
         const updatedFeedbacks = currentState.feedbacks.map((feedback) => {
           if(feedback._id !== action.feedback._id){
             //This is not the item we care about, keep it as is
             return feedback;
           }
           //Otherwise, this is the one we want to return an updated value
           return { ...feedback, ...action.feedback }
         })
          return {
            ...currentState,
            feedbacks:updatedFeedbacks,
            feedback:null,
            isFetching: false,
            error: null,
            successMsg:action.message,
            showDeleteModal: false,
            feedbackToDelete: null,
            showEditModal: true,
            feedbackToEdit: action.feedback,
            newFeedback: null
          }

  case 'EDIT_FEEDBACK_FAILED':
        return {
          ...currentState,
          feedbacks:currentState.feedbacks,
          feedback:null,
          isFetching: false,
          error: action.error,
          successMsg:null,
          showDeleteModal: false,
          feedbackToDelete: null,
          showEditModal: true,
          feedbackToEdit: currentState.feedbackToEdit,
          newFeedback: null
        }

  case 'DELETE_FEEDBACK_REQUEST':
        return {
          ...currentState,
          feedbacks:currentState.feedbacks,
          feedback:null,
          isFetching: true,
          error: null,
          successMsg:null,
          showDeleteModal: true,
          feedbackToDelete: action.feedback,
          showEditModal: false,
          feedbackToEdit: null,
          newFeedback: null
        }

  case 'DELETE_FEEDBACK_SUCCESS':
  const filteredFeedbacks = currentState.feedbacks.filter((feedback) => feedback._id !== currentState.feedbackToDelete._id)
        return {
          ...currentState,
          feedbacks:filteredFeedbacks,
          feedback:null,
          isFetching: false,
          error: null,
          successMsg:action.message,
          showDeleteModal: true,
          feedbackToDelete: null,
          showEditModal: false,
          feedbackToEdit: null,
          newFeedback: null
        }


  case 'DELETE_FEEDBACK_FAILED':
        return {
          ...currentState,
          feedbacks:currentState.feedbacks,
          feedback:null,
          isFetching: false,
          error: action.error,
          successMsg:null,
          showDeleteModal: true,
          feedbackToDelete: null,
          showEditModal: false,
          feedbackToEdit: null,
          newFeedback: null
        }

  case 'SHOW_DELETE_MODAL':
        return {
          ...currentState,
          feedbacks:currentState.feedbacks,
          feedback:null,
          isFetching: false,
          error: null,
          successMsg:null,
          showDeleteModal: true,
          feedbackToDelete: action.feedback,
          showEditModal: false,
          feedbackToEdit: null,
          newFeedback: null
        }

  case 'HIDE_DELETE_MODAL':
        return {
          ...currentState,
          feedbacks:currentState.feedbacks,
          feedback:null,
          isFetching: false,
          error: null,
          successMsg:null,
          showDeleteModal: false,
          feedbackToDelete: null,
          showEditModal: false,
          feedbackToEdit: null,
          newFeedback: null
        }


    default:
       return currentState;

  }
}



