// ./react-redux-client/src/actions/feedbackActions.js

const apiUrl = "/api/feedback/";

export const toggleAddBook = () => {
  return {
    type: 'TOGGLE_ADD_FEEDBACK'
  }
}

export const addNewFeedback = (feedback) => {console.log(feedback)
  return (dispatch) => {
    dispatch(addNewFeedbackRequest(feedback));
    return fetch(apiUrl, {
      method:'post',
      body: feedback,
    }).then(response => {
      if(response.ok){
        response.json().then(data => {console.log(data.feedback);
          dispatch(addNewFeedbackRequestSuccess(data.feedback, data.message))
        })
      }
      else{
        response.json().then(error => {
          dispatch(addNewFeedbackRequestFailed(error))
        })
      }
    })
  }
}

export const addNewFeedbackRequest = (feedback) => {
  return {
    type: 'ADD_NEW_FEEDBACK_REQUEST',
    feedback
  }
}

export const addNewFeedbackRequestSuccess = (feedback,message) => {
  return {
    type: 'ADD_NEW_FEEDBACK_REQUEST_SUCCESS',
    feedback:feedback,
    message:message
  }
}

export const addNewFeedbackRequestFailed = (error) => {
  return {
    type: 'ADD_NEW_FEEDBACK_REQUEST_FAILED',
    error
  }
}

//Async action
export const fetchFeedbacks = () => {
  // Returns a dispatcher function
  // that dispatches an action at later time
  return (dispatch) => {

    dispatch(fetchFeedbacksRequest());
    // Returns a promise
    return fetch(apiUrl)
                .then(response => {
                  if(response.ok){
                    response.json().then(data => {
                      dispatch(fetchFeedbacksSuccess(data.feedbacks,data.message));
                    })
                  }
                  else{
                    response.json().then(error => {
                      dispatch(fetchFeedbacksFailed(error));
                    })
                  }
                })


  }
}

export const fetchFeedbacksRequest = () => {
  return {
    type:'FETCH_FEEDBACKS_REQUEST'
  }
}


//Sync action
export const fetchFeedbacksSuccess = (feedbacks,message) => {
  return {
    type: 'FETCH_FEEDBACKS_SUCCESS',
    feedbacks: feedbacks,
    message: message,
    receivedAt: Date.now
  }
}

export const fetchFeedbacksFailed = (error) => {
  return {
    type:'FETCH_FEEDBACKS_FAILED',
    error
  }
}

export const fetchFeedbackById = (feedbackId) => {
  return (dispatch) => {
    dispatch(fetchFeedbackRequest());
      // Returns a promise
      return fetch(apiUrl + feedbackId)
             .then(response => {console.log(response)
               if(response.ok){
                 response.json().then(data => {
                   dispatch(fetchFeedbackSuccess(data.feedback[0], data.message));
                 })
               }
               else{
                 response.json().then(error => {
                   dispatch(fetchFeedbackFailed(error));
                 })
               }
             })

  }
}

export const fetchFeedbackRequest = () => {
  return {
    type:'FETCH_FEEDBACK_REQUEST'
  }
}


//Sync action
export const fetchFeedbackSuccess = (feedback,message) => {
  return {
    type: 'FETCH_FEEDBACK_SUCCESS',
    feedback: feedback,
    message: message,
    receivedAt: Date.now
  }
}

export const fetchFeedbackFailed = (error) => {
  return {
    type:'FETCH_FEEDBACK_FAILED',
    error
  }
}

export const showEditModal = (feedbackToEdit) => {
  return {
    type:'SHOW_EDIT_MODAL',
    feedback:feedbackToEdit
  }
}

export const hideEditModal = () => {
  return {
    type:'HIDE_EDIT_MODAL'
  }
}

export const editFeedback = (feedback) => {
    return (dispatch) => {
      dispatch(editFeedbackRequest(feedback));
      return fetch(apiUrl, {
        method:'put',
        body:feedback
      }).then(response => {
        if(response.ok){
          response.json().then(data => {
            dispatch(editFeedbackSuccess(data.feedback,data.message));
          })
        }
        else{
          response.json().then(error => {
            dispatch(editFeedbackFailed(error));
          })
        }
      })
    }
}

export const editFeedbackRequest = (feedback) => {
   return {
     type:'EDIT_FEEDBACK_REQUEST',
     feedback
   }
}

export const editFeedbackSuccess = (feedback,message) => {
  return {
    type:'EDIT_FEEDBACK_SUCCESS',
    feedback:feedback,
    message:message
  }
}

export const editFeedbackFailed = (error) => {
  return {
    type:'EDIT_FEEDBACK_FAILED',
    error
  }
}

export const deleteFeedback = (feedback) => {
  return (dispatch) => {
    dispatch(deleteFeedbackRequest(feedback));
    return fetch(apiUrl + feedback._id ,{
      method:'delete'
    }).then(response => {
      if(response.ok){
        response.json().then(data => {
          dispatch(deleteFeedbackSuccess(data.message));
        })
      }
      else{
        response.json().then(error => {
          dispatch(deleteFeedbackFailed(error));
        })
      }
    })

  }
}

export const deleteFeedbackRequest = (feedback) => {
   return {
     type:'DELETE_FEEDBACK_REQUEST',
     feedback
   }
}

export const deleteFeedbackSuccess = (message) => {
  return {
    type:'DELETE_FEEDBACK_SUCCESS',
    message:message
  }
}

export const deleteFeedbackFailed = (error) => {
  return {
    type:'DELETE_FEEDBACK_FAILED',
    error
  }
}

export const showDeleteModal = (feedbackToDelete) => {
  return {
    type:'SHOW_DELETE_MODAL',
    feedback:feedbackToDelete
  }
}

export const hideDeleteModal = () => {
  return {
    type:'HIDE_DELETE_MODAL'
  }
}



