// ./react-redux-client/src/reducers/appReducer.js
const INITIAL_STATE = {
  showAddFeedback: false
}

const appReducer = (currentState = INITIAL_STATE, action) => {
  switch (action.type) {
    case 'TOGGLE_ADD_FEEDBACK':
          return {
            ...currentState,showAddFeedback: !currentState.showAddFeedback
          }


    default:
       return currentState;

  }
}

export default appReducer;



