import { connect } from 'react-redux';
import * as feedbackActions from '../actions/feedbackActions';
import Feedbacks from '../components/Feedbacks';

// map state from store to props
const mapStateToProps = (state,ownProps) => {
  return {
    //you can now say this.props.mappedAppSate
    mappedFeedbackState: state.feedbackState
  }
}

// map actions to props
const mapDispatchToProps = (dispatch) => {
  return {
    //you can now say this.props.mappedAppActions
    fetchFeedbacks: () => dispatch(feedbackActions.fetchFeedbacks()),
    mappedEditFeedback: feedbackToEdit => dispatch(feedbackActions.editFeedback(feedbackToEdit)),
    mappedshowEditModal: feedbackToEdit => dispatch(feedbackActions.showEditModal(feedbackToEdit)),
    mappedhideEditModal: () => dispatch(feedbackActions.hideEditModal()),
    mappedDeleteFeedback: feedbackToDelete => dispatch(feedbackActions.deleteFeedback(feedbackToDelete)),
    mappedshowDeleteModal: feedbackToDelete => dispatch(feedbackActions.showDeleteModal(feedbackToDelete)),
    mappedhideDeleteModal: () => dispatch(feedbackActions.hideDeleteModal())
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(Feedbacks);



