// ./react-redux-client/src/components/Feedback.js
import React from 'react';

export default class Feedback extends React.Component {
  componentDidMount(){
    this.props.mappedfetchFeedbackById(this.props.params.id);
  }

  render(){
    const feedbackState = this.props.mappedFeedbackState;
    return(
      <div className="feedbackDetail">
       <h2>Feedback Detail</h2>
         {!feedbackState.feedback && feedbackState.isFetching &&
           <div>
             <p>Loading feedback....</p>
           </div>
         }
       {feedbackState.feedback && !feedbackState.isFetching &&
         <div>
           <h3>{feedbackState.feedback.feedbackText}</h3>
           <p>{feedbackState.feedback.feedbackDesc}</p>
         </div>
       }
      </div>
    );
  }
}



