// ./react-redux-client/src/components/FeedbackEditForm.js
import React from 'react';
import { FormGroup,ControlLabel,FormControl,Button } from 'react-bootstrap';

const FeedbackEditForm = (props) => {
  return (
    <form className="form form-horizontal" id="EditFeedbackForm" onSubmit={props.editFeedback}>
    <div className="row">
    <div className="col-md-12">
    <FormGroup>
          <ControlLabel>Feedback: </ControlLabel>
          <input type="hidden" value={props.feedbackData._id} name="id"/>
            <FormControl
              type="text" placeholder="Enter feedback"
              name="feedbackText" defaultValue={props.feedbackData.feedbackText}
               />
        </FormGroup>
        </div>
        <div className="col-md-12">
        <FormGroup>
              <ControlLabel>Description: </ControlLabel>
                <FormControl
                  componentClass="textarea" placeholder="Enter description"
                  name="feedbackDesc" defaultValue={props.feedbackData.feedbackDesc}
                   />
            </FormGroup>
            </div>
          </div>
          <FormGroup>
              <Button type="submit" bsStyle="success" bsSize="large" block>Submit</Button>
          </FormGroup>
    </form>
  );
}

export default FeedbackEditForm;



