import React from 'react';
import { Alert, Glyphicon, Button, Modal } from 'react-bootstrap';
import { Link } from 'react-router';
// import FeedbackEditForm from './FeedbackEditForm';

export default class Jobs extends React.Component {
  constructor(props) {
    super(props);
  }

  componentWillMount() { 
    this.props.fetchJobs();
  }
  
  render() {
    console.log('---mappedJobState---\n', this.props.mappedJobState);
    const jobState = this.props.mappedJobState;
    console.log('---jobState--\n', jobState);
    const jobs = jobState.jobs;
    console.log("jobs", jobs);
    return (
      <div className="col-md-12">
        <h3 className="centerAlign">Jobs</h3>
        {!jobs && jobState.isFetching &&
          <p>Loading jobs....</p>
        }
        {jobs.length <= 0 && !jobState.isFetching &&
          <p>No Jobs Available. Add A Job to List here.</p>
        }
        {jobs && jobs.length > 0 && !jobState.isFetching &&
          <table className="table booksTable">
            <thead>
              <tr><th>Job</th><th className="textCenter">Edit</th><th className="textCenter">Delete</th><th className="textCenter">View</th></tr>
            </thead>
            <tbody>
              {jobs.map((job, i) => <tr key={i}>
                <td>{job.jobTitle}</td>
              </tr>)
              }
            </tbody>
          </table>
        }
        </div>
    );
  }
}


