// ./react-redux-client/src/components/App.js
import React from 'react';
import { Navbar,Nav,NavItem } from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import './App.css';
import FeedbackForm from './FeedbackForm';

export default class App extends React.Component {
  constructor(props){
    super(props);
    this.toggleAddFeedback = this.toggleAddFeedback.bind(this);
    this.addFeedback = this.addFeedback.bind(this);
    this.handleSelect = this.handleSelect.bind(this);
  }

  toggleAddFeedback(e){
    e.preventDefault();
     this.props.mappedToggleAddFeedback();
  }
  handleSelect(e){
    // e.preventDefault();

  }

  addFeedback(e){
      e.preventDefault();
      const form = document.getElementById('addFeedbackForm');
      if(form.feedbackText.value !== ""  && form.feedbackDesc.value !== ""){
        const data = new FormData();
       data.append('feedbackText', form.feedbackText.value);
        data.append('feedbackDesc', form.feedbackDesc.value);
        // const data = {
        //   feedbackText: form.feedbackText.value,
        //   feedbackDesc: form.feedbackDesc.value
        // }
        this.props.mappedAddFeedback(data);
      form.reset();
      }
      else{
        return ;
      }
  }

  render(){
    const appState = this.props.mappedAppState;
    return(
      <div>
      <Navbar inverse  collapseOnSelect className="customNav">
    <Navbar.Header>
      <Navbar.Brand>
        <a href="/#">Mern Stack Feedback App</a>
      </Navbar.Brand>
      <Navbar.Toggle />
    </Navbar.Header>
    <Navbar.Collapse>
      <Nav>
        <LinkContainer to={{ pathname: '/', query: {  } }}>
           <NavItem eventKey={1}>Home</NavItem>
        </LinkContainer>
      </Nav>
      <Nav pullRight>
      <LinkContainer to={{ pathname: '/', query: {  } }} onClick={this.toggleAddFeedback}>
         <NavItem eventKey={1}>Add Feedback</NavItem>
      </LinkContainer>
      </Nav>
    </Navbar.Collapse>
  </Navbar>
  <Nav bsStyle="pills" stacked activeKey={1} onSelect={this.handleSelect} pullLeft>
    <LinkContainer to={{ pathname: '/jobs', query: {  } }}>
      <NavItem eventKey={1} href="/jobs">
        Jobs
      </NavItem>
    </LinkContainer>
    <LinkContainer to={{ pathname: '/feedbacks', query: {  } }}>
      <NavItem eventKey={1} href="/feedbacks">
        Feedbacks
      </NavItem>
    </LinkContainer>
    <NavItem eventKey={3} disabled>
      NavItem 3 content
    </NavItem>
  </Nav>
  
  <div className="container">
  {appState.showAddFeedback &&
    <FeedbackForm addFeedback={this.addFeedback} />
  }
  { /* Each Smaller Components */}
   {this.props.children}
  </div>
 </div>
    );
  }
}



