// ./react-redux-client/src/components/Feedbacks.js
import React from 'react';
import { Alert,Glyphicon,Button,Modal } from 'react-bootstrap';
import { Link } from 'react-router';
import FeedbackEditForm from './FeedbackEditForm';

export default class Feedbacks extends React.Component {
  constructor(props){
    super(props);
    this.hideEditModal = this.hideEditModal.bind(this);
    this.submitEditFeedback = this.submitEditFeedback.bind(this);
    this.hideDeleteModal = this.hideDeleteModal.bind(this);
    this.cofirmDeleteFeedback = this.cofirmDeleteFeedback.bind(this);
  }

  componentWillMount(){
    this.props.fetchFeedbacks();
  }


  showEditModal(feedbackToEdit){
     this.props.mappedshowEditModal(feedbackToEdit);
  }

  hideEditModal(){
     this.props.mappedhideEditModal();
  }

  submitEditFeedback(e){
    e.preventDefault();
    const editForm = document.getElementById('EditFeedbackForm');
    if(editForm.feedbackText.value !== ""){
      const data = new FormData();
      data.append('id', editForm.id.value);
     data.append('feedbackText', editForm.feedbackText.value);
      data.append('feedbackDesc', editForm.feedbackDesc.value);
      this.props.mappedEditFeedback(data);
    }
    else{
      return;
    }
  }

  hideDeleteModal(){
    this.props.mappedhideDeleteModal();
  }

  showDeleteModal(feedbackToDelete){
    this.props.mappedshowDeleteModal(feedbackToDelete);
  }

  cofirmDeleteFeedback(){
    this.props.mappedDeleteFeedback(this.props.mappedFeedbackState.feedbackToDelete);
  }

  render(){
    const feedbackState = this.props.mappedFeedbackState;
    const feedbacks = feedbackState.feedbacks;
    const editFeedback = feedbackState.feedbackToEdit;
    return(
      <div className="col-md-12">
      <h3 className="centerAlign">Feedbacks</h3>
      {!feedbacks && feedbackState.isFetching &&
        <p>Loading feedbacks....</p>
      }
      {feedbacks.length <= 0 && !feedbackState.isFetching &&
        <p>No Feedbacks Available. Add A Feedback to List here.</p>
      }
      {feedbacks && feedbacks.length > 0 && !feedbackState.isFetching &&
      <table className="table booksTable">
      <thead>
       <tr><th>Feedback</th><th className="textCenter">Edit</th><th className="textCenter">Delete</th><th className="textCenter">View</th></tr>
      </thead>
      <tbody>
        {feedbacks.map((feedback,i) => <tr key={i}>
        <td>{feedback.feedbackText}</td>
         <td className="textCenter"><Button onClick={() => this.showEditModal(feedback)} bsStyle="info" bsSize="xsmall"><Glyphicon glyph="pencil" /></Button></td>
         <td className="textCenter"><Button onClick={() => this.showDeleteModal(feedback)} bsStyle="danger" bsSize="xsmall"><Glyphicon glyph="trash" /></Button></td>
         <td className="textCenter"><Link to={`/${feedback._id}`}>View Details</Link> </td>
         </tr> )
      }
      </tbody>
      </table>
    }

    {/* Modal for editing feedback */}
    <Modal
      show={feedbackState.showEditModal}
      onHide={this.hideEditModal}
      container={this}
      aria-labelledby="contained-modal-title"
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title">Edit Your Feedback</Modal.Title>
      </Modal.Header>
      <Modal.Body>
    <div className="col-md-12">
    {editFeedback  &&
    <FeedbackEditForm feedbackData={editFeedback} editFeedback={this.submitEditFeedback} />
    }
    {editFeedback  && feedbackState.isFetching &&
      <Alert bsStyle="info">
  <strong>Updating...... </strong>
      </Alert>
    }
    {editFeedback  && !feedbackState.isFetching && feedbackState.error &&
      <Alert bsStyle="danger">
  <strong>Failed. {feedbackState.error} </strong>
      </Alert>
    }
    {editFeedback  && !feedbackState.isFetching && feedbackState.successMsg &&
      <Alert bsStyle="success">
  Book <strong> {editFeedback.feedbackText} </strong>{feedbackState.successMsg}
      </Alert>
    }
    </div>
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={this.hideEditModal}>Close</Button>
      </Modal.Footer>
    </Modal>

{/* Modal for deleting feedback */}
    <Modal
    show={feedbackState.showDeleteModal}
    onHide={this.hideDeleteModal}
    container={this}
    aria-labelledby="contained-modal-title"
  >
    <Modal.Header closeButton>
      <Modal.Title id="contained-modal-title">Delete Your Book</Modal.Title>
    </Modal.Header>
    <Modal.Body>
    {feedbackState.feedbackToDelete && !feedbackState.error && !feedbackState.isFetching &&
      <Alert bsStyle="warning">
 Are you sure you want to delete this feedback <strong>{feedbackState.feedbackToDelete.feedbackText} </strong> ?
</Alert>
    }
    {feedbackState.feedbackToDelete && feedbackState.error &&
      <Alert bsStyle="warning">
 Failed. <strong>{feedbackState.error} </strong>
</Alert>
    }

    {feedbackState.feedbackToDelete && !feedbackState.error && feedbackState.isFetching &&
      <Alert bsStyle="success">
  <strong>Deleting.... </strong>
</Alert>
    }

    {!feedbackState.feedbackToDelete && !feedbackState.error && !feedbackState.isFetching&&
      <Alert bsStyle="success">
 Feedback <strong>{feedbackState.successMsg} </strong>
</Alert>
    }
    </Modal.Body>
    <Modal.Footer>
     {!feedbackState.successMsg && !feedbackState.isFetching &&
       <div>
       <Button onClick={this.cofirmDeleteFeedback}>Yes</Button>
       <Button onClick={this.hideDeleteModal}>No</Button>
       </div>
    }
    {feedbackState.successMsg && !feedbackState.isFetching &&
      <Button onClick={this.hideDeleteModal}>Close</Button>
    }
    </Modal.Footer>
  </Modal>
      </div>
    );
  }
}



